@import "basic";
@import "mixins";

$body: white;
$padding: 33%;
$anchor: #46f;
$nav: #181818;
$headline: #06f;
$description: white;
$odd: #ffffff;
$even: #222222;

body {
    @include body_background($body, $padding);
    > header, > footer {
        @include contrast($nav); }
    > div {
        @include contrast($description); } }



body > header h1 {
    font-weight: bold; }

h1, h2, h3, h4, h5, h6 {
    font-family: "Gentium Basic", serif;
    letter-spacing: 2px;
    header & {
        text-transform: none; } }





article:nth-of-type(even) {
    @include article_background($even, $anchor); }

article:nth-of-type(odd) {
    @include article_background($odd, $anchor); }

@import "vendor";
